@import "darker";
@import "lighter";

main {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;

    h1 {
        font-family: 'Russo One', sans-serif;
        letter-spacing: 2px;
    }

    .email {
        margin-bottom: 1rem;
    }

    .intro {
        font-size: large;
    }

    .list {
        list-style: none;
        padding: 0;
        display: flex;
        justify-content: center;
    }

    .list li:not(:last-child) {
        margin-right: .8rem;
    }
}
